import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { ArrowLeft, ArrowRight, HelpCircle } from 'lucide-react';
import { motion } from 'framer-motion';
import logo from '../assets/images/logo.png';

const CostsForm = () => {
  const navigate = useNavigate();
  
  // Form state
  const [formData, setFormData] = useState({
    inventory: {
      initialValue: '0.00',
      totalPurchases1: '0.00',
      totalPurchases2: '0.00',
      cogs: '0.00'
    },
    services: {
      cost: '0.00'
    }
  });

  // Summary state
  const [summary, setSummary] = useState({
    totalRevenue: '0.00',
    totalCosts: '0.00',
    totalExpenses: '0.00',
    netIncome: '0.00'
  });

  // Handle numeric input changes with formatting
  const handleInputChange = (section, field, value) => {
    // Remove non-numeric characters except decimal
    let numericValue = value.replace(/[^0-9.]/g, '');
    
    // Ensure only one decimal point
    const parts = numericValue.split('.');
    if (parts.length > 2) {
      numericValue = parts[0] + '.' + parts.slice(1).join('');
    }

    // Update form data
    setFormData(prev => ({
      ...prev,
      [section]: {
        ...prev[section],
        [field]: numericValue
      }
    }));
  };

  // Handle blur event for formatting
  const handleInputBlur = (section, field, value) => {
    const formattedValue = parseFloat(value || 0).toLocaleString('en-US', {
      minimumFractionDigits: 2,
      maximumFractionDigits: 2
    });

    setFormData(prev => ({
      ...prev,
      [section]: {
        ...prev[section],
        [field]: formattedValue
      }
    }));
  };

  // Calculate COGS whenever inventory values change
  useEffect(() => {
    const calculateCOGS = () => {
      const initial = parseFloat(formData.inventory.initialValue.replace(/,/g, '')) || 0;
      const purchases = parseFloat(formData.inventory.totalPurchases1.replace(/,/g, '')) || 0;
      const final = parseFloat(formData.inventory.totalPurchases2.replace(/,/g, '')) || 0;
      
      const cogs = initial + purchases - final;
      
      setFormData(prev => ({
        ...prev,
        inventory: {
          ...prev.inventory,
          cogs: cogs.toLocaleString('en-US', {
            minimumFractionDigits: 2,
            maximumFractionDigits: 2
          })
        }
      }));
    };

    calculateCOGS();
  }, [formData.inventory.initialValue, formData.inventory.totalPurchases1, formData.inventory.totalPurchases2]);

  // Calculate totals when form data changes
  useEffect(() => {
    const calculateTotalCosts = () => {
      const cogsCosts = parseFloat(formData.inventory.cogs.replace(/,/g, '')) || 0;
      const serviceCosts = parseFloat(formData.services.cost.replace(/,/g, '')) || 0;
      
      const totalCosts = cogsCosts + serviceCosts;

      setSummary(prev => ({
        ...prev,
        totalCosts: totalCosts.toLocaleString('en-US', {
          minimumFractionDigits: 2,
          maximumFractionDigits: 2
        }),
        netIncome: (
          parseFloat(prev.totalRevenue.replace(/,/g, '')) - 
          totalCosts - 
          parseFloat(prev.totalExpenses.replace(/,/g, ''))
        ).toLocaleString('en-US', {
          minimumFractionDigits: 2,
          maximumFractionDigits: 2
        })
      }));
    };

    calculateTotalCosts();
  }, [formData]);

  const handlePrevious = () => {
    navigate('/income');
  };

  const handleNext = () => {
    navigate('/TaxQuestions');
  };

  return (
    <motion.div
      initial={{ opacity: 0, x: 20 }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: -20 }}
      transition={{ duration: 0.3 }}
      className="min-h-screen bg-white flex flex-col"
    >
      {/* Logo Section */}
      <div className="p-8 border-b">
        <div className="w-12 h-12">
          <img src={logo} alt="Company Logo" className="w-full h-full object-contain" />
        </div>
      </div>

      {/* Main Content */}
      <div className="flex flex-1 w-full max-w-7xl mx-auto px-8 py-12 gap-8">
        {/* Left Column - Form */}
        <div className="w-1/2">
          <h1 className="text-3xl font-bold text-gray-900 mb-6">
            What costs did your company have?
          </h1>

          <p className="text-lg text-gray-700 mb-8">
            Please input your information in the following fields:
          </p>

          <div className="space-y-8">
            {/* Inventory and Cost of Goods Sold Section */}
            <div className="p-6 rounded-lg space-y-4" style={{ backgroundColor: 'rgba(0, 151, 151, 0.24)' }}>
              <div className="flex items-center gap-2">
                <h2 className="font-semibold text-gray-900">
                  Inventory and Cost of Goods Sold
                </h2>
              </div>
              
              <div className="space-y-4">
                <div className="space-y-2">
                  <div className="flex items-center gap-2">
                    <label className="block text-sm font-medium text-gray-700">
                      Initial Inventory Value (01 Jan 2023)
                    </label>
                    <div className="relative group">
                      <HelpCircle size={16} className="text-red-400 cursor-help" />
                      <div className="absolute left-1/2 -translate-x-1/2 bottom-full mb-2 w-72 p-2 bg-gray-800 text-white text-sm rounded-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-opacity">
                        The total value of your inventory at the beginning of the year. This includes all products, materials, and goods in stock as of January 1st, 2023.
                        <div className="absolute left-1/2 -bottom-1 -translate-x-1/2 w-2 h-2 bg-gray-800 rotate-45"></div>
                      </div>
                    </div>
                  </div>
                  <div className="relative">
                    <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
                    <input
                      type="text"
                      value={formData.inventory.initialValue}
                      onChange={(e) => handleInputChange('inventory', 'initialValue', e.target.value)}
                      onBlur={(e) => handleInputBlur('inventory', 'initialValue', e.target.value)}
                      className="w-full pl-8 pr-4 py-2 bg-white border rounded-lg focus:ring-2 focus:ring-teal-500 focus:border-teal-500"
                    />
                  </div>
                </div>

                <div className="space-y-2">
                  <div className="flex items-center gap-2">
                    <label className="block text-sm font-medium text-gray-700">
                      Total Purchases in 2023
                    </label>
                    <div className="relative group">
                      <HelpCircle size={16} className="text-red-400 cursor-help" />
                      <div className="absolute left-1/2 -translate-x-1/2 bottom-full mb-2 w-72 p-2 bg-gray-800 text-white text-sm rounded-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-opacity">
                        The total cost of all inventory purchases made during 2023, including raw materials, finished goods, and any other items added to your inventory.
                        <div className="absolute left-1/2 -bottom-1 -translate-x-1/2 w-2 h-2 bg-gray-800 rotate-45"></div>
                      </div>
                    </div>
                  </div>
                  <div className="relative">
                    <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
                    <input
                      type="text"
                      value={formData.inventory.totalPurchases1}
                      onChange={(e) => handleInputChange('inventory', 'totalPurchases1', e.target.value)}
                      onBlur={(e) => handleInputBlur('inventory', 'totalPurchases1', e.target.value)}
                      className="w-full pl-8 pr-4 py-2 bg-white border rounded-lg focus:ring-2 focus:ring-teal-500 focus:border-teal-500"
                    />
                  </div>
                </div>

                <div className="space-y-2">
                  <div className="flex items-center gap-2">
                    <label className="block text-sm font-medium text-gray-700">
                      Final Inventory Value (31 Dec 2023)
                    </label>
                    <div className="relative group">
                      <HelpCircle size={16} className="text-red-400 cursor-help" />
                      <div className="absolute left-1/2 -translate-x-1/2 bottom-full mb-2 w-72 p-2 bg-gray-800 text-white text-sm rounded-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-opacity">
                        The total value of your inventory at the end of the year. This is the value of all remaining products, materials, and goods in stock as of December 31st, 2023.
                        <div className="absolute left-1/2 -bottom-1 -translate-x-1/2 w-2 h-2 bg-gray-800 rotate-45"></div>
                      </div>
                    </div>
                  </div>
                  <div className="relative">
                    <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
                    <input
                      type="text"
                      value={formData.inventory.totalPurchases2}
                      onChange={(e) => handleInputChange('inventory', 'totalPurchases2', e.target.value)}
                      onBlur={(e) => handleInputBlur('inventory', 'totalPurchases2', e.target.value)}
                      className="w-full pl-8 pr-4 py-2 bg-white border rounded-lg focus:ring-2 focus:ring-teal-500 focus:border-teal-500"
                    />
                  </div>
                </div>

                {/* Cost of Goods Sold - Calculated Field */}
                <div className="space-y-2">
                  <div className="flex items-center gap-2">
                    <label className="block text-sm font-medium text-gray-700">
                      Cost of Goods Sold
                    </label>
                    <div className="relative group">
                      <HelpCircle size={16} className="text-red-400 cursor-help" />
                      <div className="absolute left-1/2 -translate-x-1/2 bottom-full mb-2 w-72 p-2 bg-gray-800 text-white text-sm rounded-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-opacity">
                        Automatically calculated value representing the total cost of goods sold during 2023. Formula: Initial Inventory + Total Purchases - Final Inventory = Cost of Goods Sold
                        <div className="absolute left-1/2 -bottom-1 -translate-x-1/2 w-2 h-2 bg-gray-800 rotate-45"></div>
                      </div>
                    </div>
                  </div>
                  <div className="relative">
                    <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
                    <input
                      type="text"
                      value={formData.inventory.cogs}
                      readOnly
                      className="w-full pl-8 pr-4 py-2 bg-gray-50 border rounded-lg text-gray-700"
                    />
                  </div>
                  <p className="text-xs text-gray-500 italic">
                    Initial Inventory + Total Purchases - Final Inventory
                  </p>
                </div>
              </div>
            </div>

            {/* Cost Related to Services Section */}
            <div className="p-6 rounded-lg space-y-4" style={{ backgroundColor: 'rgba(110, 64, 129, 0.4)' }}>
              <div className="flex items-center gap-2">
                <h2 className="font-semibold text-gray-900">
                  Cost related to services
                </h2>
              </div>
              <div className="space-y-2">
                <div className="flex items-center gap-2">
                  <label className="block text-sm font-medium text-gray-700">
                    Cost of services provided
                  </label>
                  <div className="relative group">
                    <HelpCircle size={16} className="text-red-400 cursor-help" />
                    <div className="absolute left-1/2 -translate-x-1/2 bottom-full mb-2 w-72 p-2 bg-gray-800 text-white text-sm rounded-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-opacity">
                      The total direct costs incurred in providing services to customers, including labor, materials, and other direct service-related expenses.
                      <div className="absolute left-1/2 -bottom-1 -translate-x-1/2 w-2 h-2 bg-gray-800 rotate-45"></div>
                    </div>
                  </div>
                </div>
                <div className="relative">
                  <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
                  <input
                    type="text"
                    value={formData.services.cost}
                    onChange={(e) => handleInputChange('services', 'cost', e.target.value)}
                    onBlur={(e) => handleInputBlur('services', 'cost', e.target.value)}
                    className="w-full pl-8 pr-4 py-2 bg-white border rounded-lg focus:ring-2 focus:ring-teal-500 focus:border-teal-500"
                  />
                </div>
              </div>
            </div>
          </div>
          {/* Progress and Navigation */}
          <div className="mt-12">
            <div className="flex items-center mb-4">
              <div className="text-sm text-gray-500">Step 3 of 9</div>
              <div className="ml-4 flex-1 h-2 bg-gray-200 rounded-full">
                <div
                  className="h-full bg-teal-500 rounded-full transition-all duration-300 ease-in-out"
                  style={{ width: "33.3%" }}
                ></div>
              </div>
            </div>
            <div className="flex gap-4">
              <button 
                onClick={handlePrevious}
                className="px-6 py-2 rounded-lg bg-purple-600 text-white flex items-center gap-2 hover:bg-purple-700 transition-colors"
              >
                <ArrowLeft size={20} />
                Previous
              </button>
              <button 
                onClick={handleNext}
                className="px-6 py-2 rounded-lg bg-teal-500 text-white flex items-center gap-2 hover:bg-teal-600 transition-colors"
              >
                Next
                <ArrowRight size={20} />
              </button>
            </div>
          </div>
        </div>

        {/* Right Column - Summary */}
        <div className="w-1/2 p-8 rounded-lg" style={{ backgroundColor: '#F7F7F7' }}>
          <h2 className="text-2xl font-bold text-gray-900 mb-8 text-center">
          2023 SUMMARY NET INCOME
          </h2>

          <div className="space-y-4">
            <div className="flex justify-between p-4 bg-white rounded-lg">
              <span className="font-medium">TOTAL REVENUE</span>
              <span className="font-medium">${summary.totalRevenue}</span>
            </div>

            <div className="flex justify-between p-4 rounded-lg" style={{ backgroundColor: 'rgba(65, 191, 120, 0.19)' }}>
              <span className="font-bold">TOTAL COSTS</span>
              <span className="font-bold">${summary.totalCosts}</span>
            </div>

            <div className="flex justify-between p-4 bg-white rounded-lg">
              <span className="font-medium">TOTAL EXPENSES</span>
              <span className="font-medium">${summary.totalExpenses}</span>
            </div>

            <div className="flex justify-between p-4 rounded-lg" style={{ backgroundColor: 'rgba(65, 191, 120, 0.19)' }}>
              <span className="font-bold">NET INCOME</span>
              <span className="font-bold">${summary.netIncome}</span>
            </div>
          </div>
        </div>
      </div>
    </motion.div>
  );
};

export default CostsForm;