import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { ArrowLeft, ArrowRight, Plus, X, Trash2, HelpCircle } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import logo from '../assets/images/logo.png';

// Helper function to format number with commas and two decimal places
const formatNumber = (value) => {
  const cleanValue = value.replace(/,/g, '');
  if (!cleanValue || isNaN(cleanValue)) return '0.00';
  
  return Number(cleanValue).toLocaleString('en-US', {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2
  });
};

// Helper function to unformat number (remove commas) for calculations
const unformatNumber = (value) => {
  return value.replace(/,/g, '');
};

// Modal Component
const AddExpenseModal = ({ isOpen, onClose, onAdd }) => {
  const [description, setDescription] = useState('');
  const [amount, setAmount] = useState('0.00');

  const handleAmountChange = (value) => {
    let numericValue = value.replace(/[^0-9.]/g, '');
    
    const parts = numericValue.split('.');
    if (parts.length > 2) {
      numericValue = parts[0] + '.' + parts.slice(1).join('');
    }

    setAmount(numericValue);
  };

  const handleAmountBlur = () => {
    setAmount(formatNumber(amount));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    onAdd({ description, amount: formatNumber(amount) });
    setDescription('');
    setAmount('0.00');
    onClose();
  };

  if (!isOpen) return null;

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4"
    >
      <motion.div
        initial={{ scale: 0.95 }}
        animate={{ scale: 1 }}
        exit={{ scale: 0.95 }}
        className="bg-white rounded-lg p-8 w-full max-w-2xl" // Increased padding and max-width
      >
        <div className="flex justify-between items-center mb-6"> {/* Increased margin bottom */}
          <h3 className="text-2xl font-bold"> {/* Increased text size */}
            Add New Expense
          </h3>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700"
          >
            <X size={24} /> {/* Increased icon size */}
          </button>
        </div>

        {/* Warning Section */}
        <div className="mb-8 bg-gray-50 p-6 rounded-lg"> {/* Increased padding and margin */}
          <h4 className="text-2xl font-medium text-gray-900 mb-3"> {/* Increased text size and margin */}
            What to include
          </h4>
          <p className="text-xl leading-relaxed text-gray-600"> {/* Increased text size and line height */}
            Business expenses do not include <span className="text-red-500 font-medium">rent</span>, <span className="text-red-500 font-medium">partner salaries</span>, or any payments made from or received of a partner's personal account. Additionally, <span className="text-red-500 font-medium">federal tax payments</span> cannot be included as business expenses.
          </p>
        </div>

        <form onSubmit={handleSubmit} className="space-y-6"> {/* Increased spacing */}
          <div>
            <label className="block text-base font-medium text-gray-700 mb-2"> {/* Increased text size and margin */}
              Description
            </label>
            <input
              type="text"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              className="w-full p-3 text-base border rounded-lg focus:ring-2 focus:ring-teal-500" // Increased padding and text size
              required
            />
          </div>

          <div>
            <label className="block text-base font-medium text-gray-700 mb-2"> {/* Increased text size and margin */}
              Amount
            </label>
            <div className="relative">
              <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500 text-lg">$</span> {/* Increased text size */}
              <input
                type="text"
                value={amount}
                onChange={(e) => handleAmountChange(e.target.value)}
                onBlur={handleAmountBlur}
                className="w-full pl-8 p-3 text-base border rounded-lg focus:ring-2 focus:ring-teal-500" // Increased padding and text size
                required
              />
            </div>
          </div>

          <div className="flex justify-end gap-4 mt-8"> {/* Increased gap and margin */}
            <button
              type="button"
              onClick={onClose}
              className="px-6 py-3 text-base font-medium text-gray-600 hover:text-gray-800" // Increased padding and text size
            >
              Cancel
            </button>
            <button
              type="submit"
              className="px-6 py-3 text-base font-medium bg-teal-500 text-white rounded-lg hover:bg-teal-600" // Increased padding and text size
            >
              Add Expense
            </button>
          </div>
        </form>
      </motion.div>
    </motion.div>
  );
};

const ExpensesForm = () => {
  const navigate = useNavigate();
  const [isModalOpen, setIsModalOpen] = useState(false);
  
  // Initial expenses list
  const [expenses, setExpenses] = useState([
    { id: 1, description: 'Advertising', amount: '0.00' },
    { id: 2, description: 'Accounting', amount: '0.00' },
    { id: 3, description: 'Bank services charges (card fees)', amount: '0.00' },
    { id: 4, description: 'Company fees and licenses', amount: '0.00' },
    { id: 5, description: 'Consulting services', amount: '0.00' },
    { id: 6, description: 'Globalfy plan subscription', amount: '0.00' },
    { id: 7, description: 'Interest expenses', amount: '0.00' },
    { id: 8, description: 'Administrative expenses', amount: '0.00' }
  ]);

  // Summary state
  const [summary, setSummary] = useState({
    totalRevenue: '0.00',
    totalCosts: '0.00',
    totalExpenses: '0.00',
    netIncome: '0.00'
  });

  // Handle amount change for existing expenses
  const handleAmountChange = (id, value) => {
    let numericValue = value.replace(/[^0-9.]/g, '');
    
    const parts = numericValue.split('.');
    if (parts.length > 2) {
      numericValue = parts[0] + '.' + parts.slice(1).join('');
    }

    setExpenses(prev => prev.map(expense => 
      expense.id === id ? { ...expense, amount: numericValue } : expense
    ));
  };

  // Handle blur event for formatting
  const handleAmountBlur = (id) => {
    setExpenses(prev => prev.map(expense => 
      expense.id === id ? { ...expense, amount: formatNumber(expense.amount) } : expense
    ));
  };

  // Handle adding new expense
  const handleAddExpense = (newExpense) => {
    setExpenses(prev => [...prev, {
      id: Math.max(...prev.map(e => e.id)) + 1,
      description: newExpense.description,
      amount: newExpense.amount
    }]);
  };

  // Handle removing expense
  const handleRemoveExpense = (id) => {
    setExpenses(prev => prev.filter(expense => expense.id !== id));
  };

  // Calculate totals when expenses change
  useEffect(() => {
    const totalExpenses = expenses.reduce((sum, expense) => 
      sum + (parseFloat(unformatNumber(expense.amount)) || 0), 0
    );

    setSummary(prev => ({
      ...prev,
      totalExpenses: formatNumber(totalExpenses.toString()),
      netIncome: formatNumber((
        parseFloat(unformatNumber(prev.totalRevenue)) - 
        parseFloat(unformatNumber(prev.totalCosts)) - 
        totalExpenses
      ).toString())
    }));
  }, [expenses]);

  const handlePrevious = () => {
    navigate('/TaxQuestions');
  };

  const handleNext = () => {
    navigate('/partners');
  };

  return (
    <motion.div
      initial={{ opacity: 0, x: 20 }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: -20 }}
      transition={{ duration: 0.3 }}
      className="min-h-screen bg-white flex flex-col"
    >
      <AnimatePresence>
        {isModalOpen && (
          <AddExpenseModal
            isOpen={isModalOpen}
            onClose={() => setIsModalOpen(false)}
            onAdd={handleAddExpense}
          />
        )}
      </AnimatePresence>

      {/* Logo Section */}
      <div className="p-8 border-b">
        <div className="w-12 h-12">
          <img src={logo} alt="Company Logo" className="w-full h-full object-contain" />
        </div>
      </div>

      {/* Main Content */}
      <div className="flex flex-1 w-full max-w-7xl mx-auto px-8 py-12 gap-8">
        {/* Left Column - Form */}
        <div className="w-1/2">
          <div className="mb-6">
            <div className="flex items-center gap-1">
              <h1 className="text-3xl font-bold text-gray-900">
                What expenses did your company have?
              </h1>
              <div className="relative group mt-2">
                <HelpCircle size={20} className="text-red-400 cursor-help" />
                <div className="absolute left-1/2 -translate-x-1/2 bottom-full mb-2 w-80 p-3 bg-gray-800 text-white text-sm rounded-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-opacity z-10">
                  Business expenses are costs incurred in the ordinary course of business. These include operational costs like advertising, banking fees, licenses, and administrative expenses. They are deducted from revenue to determine net income and are usually tax-deductible.
                  <div className="absolute left-1/2 -bottom-1 -translate-x-1/2 w-2 h-2 bg-gray-800 rotate-45"></div>
                </div>
              </div>
            </div>
          </div>

          <p className="text-lg text-gray-700 mb-8">
            Please input your information in the following fields:
          </p>

          {/* Add New Item Button */}
          <button
            onClick={() => setIsModalOpen(true)}
            className="flex items-center gap-2 text-teal-500 hover:text-teal-600 mb-6"
          >
            <Plus size={20} />
            Add a new item
          </button>

          {/* Expenses List */}
          <div className="max-h-[400px] overflow-y-auto pr-4 space-y-4">
            {expenses.map((expense) => (
              <div 
                key={expense.id}
                className="flex items-center gap-4"
              >
                <div className="flex-1">
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    {expense.description}
                  </label>
                  <div className="relative">
                    <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
                    <input
                      type="text"
                      value={expense.amount}
                      onChange={(e) => handleAmountChange(expense.id, e.target.value)}
                      onBlur={() => handleAmountBlur(expense.id)}
                      className="w-full pl-8 pr-4 py-2 border rounded-lg focus:ring-2 focus:ring-teal-500"
                    />
                  </div>
                </div>
                <button
                  onClick={() => handleRemoveExpense(expense.id)}
                  className="text-gray-400 hover:text-red-500 transition-colors mt-6"
                >
                  <Trash2 size={20} />
                </button>
              </div>
            ))}
          </div>

          {/* Progress and Navigation */}
          <div className="mt-12">
            <div className="flex items-center mb-4">
              <div className="text-sm text-gray-500">Step 5 of 9</div>
              <div className="ml-4 flex-1 h-2 bg-gray-200 rounded-full">
                <div
                  className="h-full bg-teal-500 rounded-full transition-all duration-300 ease-in-out"
                  style={{ width: "55%" }}
                ></div>
              </div>
            </div>
            <div className="flex gap-4">
              <button 
                onClick={handlePrevious}
                className="px-6 py-2 rounded-lg bg-purple-600 text-white flex items-center gap-2 hover:bg-purple-700 transition-colors"
              >
                <ArrowLeft size={20} />
                Previous
              </button>
              <button 
                onClick={handleNext}
                className="px-6 py-2 rounded-lg bg-teal-500 text-white flex items-center gap-2 hover:bg-teal-600 transition-colors"
              >
                Next
                <ArrowRight size={20} />
              </button>
            </div>
          </div>
        </div>

        {/* Right Column - Summary */}
        <div className="w-1/2 p-8 rounded-lg" style={{ backgroundColor: '#F7F7F7' }}>
          <h2 className="text-2xl font-bold text-gray-900 mb-8 text-center">
          2023 SUMMARY NET INCOME
          </h2>

          <div className="space-y-4">
            <div className="flex justify-between p-4 bg-white rounded-lg">
              <span className="font-medium">TOTAL REVENUE</span>
              <span className="font-medium">${summary.totalRevenue}</span>
            </div>

            <div className="flex justify-between p-4 bg-white rounded-lg">
              <span className="font-medium">TOTAL COSTS</span>
              <span className="font-medium">${summary.totalCosts}</span>
            </div>

            <div className="flex justify-between p-4 rounded-lg" style={{ backgroundColor: 'rgba(65, 191, 120, 0.19)' }}>
              <span className="font-bold">TOTAL EXPENSES</span>
              <span className="font-bold">${summary.totalExpenses}</span>
            </div>

            <div className="flex justify-between p-4 rounded-lg" style={{ backgroundColor: 'rgba(65, 191, 120, 0.19)' }}>
              <span className="font-bold">NET INCOME</span>
              <span className="font-bold">${summary.netIncome}</span>
            </div>
          </div>
        </div>
      </div>
    </motion.div>
  );
};

export default ExpensesForm;