import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { ArrowLeft, ArrowRight, Plus, X, Trash2, HelpCircle } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import logo from '../assets/images/logo.png';

// Helper function to format number with commas and two decimal places
const formatNumber = (value) => {
  const cleanValue = value.replace(/,/g, '');
  if (!cleanValue || isNaN(cleanValue)) return '0.00';
  
  return Number(cleanValue).toLocaleString('en-US', {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2
  });
};

// Helper function to unformat number (remove commas) for calculations
const unformatNumber = (value) => {
  return value.replace(/,/g, '');
};

// Modal Component for Adding Income
const AddIncomeModal = ({ isOpen, onClose, onAdd }) => {
  const [description, setDescription] = useState('');
  const [amount, setAmount] = useState('0.00');

  const handleSubmit = (e) => {
    e.preventDefault();
    onAdd({ description, amount: formatNumber(amount) });
    setDescription('');
    setAmount('0.00');
    onClose();
  };

  const handleAmountChange = (value) => {
    let numericValue = value.replace(/[^0-9.]/g, '');
    
    const parts = numericValue.split('.');
    if (parts.length > 2) {
      numericValue = parts[0] + '.' + parts.slice(1).join('');
    }

    setAmount(numericValue);
  };

  const handleAmountBlur = () => {
    setAmount(formatNumber(amount));
  };

  if (!isOpen) return null;

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50"
    >
      <motion.div
        initial={{ scale: 0.95 }}
        animate={{ scale: 1 }}
        exit={{ scale: 0.95 }}
        className="bg-white rounded-lg p-6 w-full max-w-md"
      >
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-lg font-semibold">Add Other Income</h3>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700"
          >
            <X size={20} />
          </button>
        </div>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Description
            </label>
            <input
              type="text"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              className="w-full p-2 border rounded-lg focus:ring-2 focus:ring-teal-500"
              required
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Amount
            </label>
            <div className="relative">
              <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
              <input
                type="text"
                value={amount}
                onChange={(e) => handleAmountChange(e.target.value)}
                onBlur={handleAmountBlur}
                className="w-full pl-8 p-2 border rounded-lg focus:ring-2 focus:ring-teal-500"
                required
              />
            </div>
          </div>

          <div className="flex justify-end gap-3 mt-6">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 text-gray-600 hover:text-gray-800"
            >
              Cancel
            </button>
            <button
              type="submit"
              className="px-4 py-2 bg-teal-500 text-white rounded-lg hover:bg-teal-600"
            >
              Add Income
            </button>
          </div>
        </form>
      </motion.div>
    </motion.div>
  );
};

// Main Income Form Component
const IncomeForm = () => {
  const navigate = useNavigate();
  const [isModalOpen, setIsModalOpen] = useState(false);
  
  // Form state
  const [formData, setFormData] = useState({
    services: '0.00',
    products: '0.00',
    interest: '0.00'
  });

  // Other income items state
  const [otherIncomes, setOtherIncomes] = useState([]);

  // Summary state
  const [summary, setSummary] = useState({
    totalRevenue: '0.00',
    totalCosts: '0.00',
    totalExpenses: '0.00',
    netIncome: '0.00'
  });

  // Handle input change for main form fields
  const handleInputChange = (field, value) => {
    let numericValue = value.replace(/[^0-9.]/g, '');
    
    const parts = numericValue.split('.');
    if (parts.length > 2) {
      numericValue = parts[0] + '.' + parts.slice(1).join('');
    }

    setFormData(prev => ({
      ...prev,
      [field]: numericValue
    }));
  };

  // Handle blur event for main form fields
  const handleInputBlur = (field) => {
    setFormData(prev => ({
      ...prev,
      [field]: formatNumber(prev[field])
    }));
  };

  // Handle adding new other income
  const handleAddIncome = (newIncome) => {
    setOtherIncomes(prev => [...prev, {
      id: Math.random().toString(36).substr(2, 9),
      ...newIncome
    }]);
  };

  // Handle removing other income
  const handleRemoveIncome = (id) => {
    setOtherIncomes(prev => prev.filter(income => income.id !== id));
  };

  // Handle amount change for other incomes
  const handleOtherAmountChange = (id, value) => {
    let numericValue = value.replace(/[^0-9.]/g, '');
    
    const parts = numericValue.split('.');
    if (parts.length > 2) {
      numericValue = parts[0] + '.' + parts.slice(1).join('');
    }

    setOtherIncomes(prev => prev.map(item => 
      item.id === id ? { ...item, amount: numericValue } : item
    ));
  };

  // Handle blur event for other incomes
  const handleOtherAmountBlur = (id) => {
    setOtherIncomes(prev => prev.map(item => 
      item.id === id ? { ...item, amount: formatNumber(item.amount) } : item
    ));
  };

  // Calculate totals when form data or other incomes change
  useEffect(() => {
    const calculateTotal = () => {
      const mainIncome = [
        parseFloat(unformatNumber(formData.services)) || 0,
        parseFloat(unformatNumber(formData.products)) || 0,
        parseFloat(unformatNumber(formData.interest)) || 0
      ].reduce((a, b) => a + b, 0);

      const otherIncomesTotal = otherIncomes.reduce((sum, income) => 
        sum + (parseFloat(unformatNumber(income.amount)) || 0), 0
      );

      const totalRevenue = mainIncome + otherIncomesTotal;

      setSummary(prev => ({
        ...prev,
        totalRevenue: formatNumber(totalRevenue.toString()),
        netIncome: formatNumber((totalRevenue - (
          parseFloat(unformatNumber(prev.totalCosts)) + 
          parseFloat(unformatNumber(prev.totalExpenses))
        )).toString())
      }));
    };

    calculateTotal();
  }, [formData, otherIncomes]);

  const handlePrevious = () => {
    navigate('/');
  };

  const handleNext = () => {
    navigate('/costs');
  };

  return (
    <motion.div
      initial={{ opacity: 0, x: 20 }}
      animate={{ opacity: 1, x: 0 }}
      exit={{ opacity: 0, x: -20 }}
      transition={{ duration: 0.3 }}
      className="min-h-screen bg-white flex flex-col"
    >
      {/* Logo Section */}
      <div className="p-8 border-b">
        <div className="w-12 h-12">
          <img src={logo} alt="Company Logo" className="w-full h-full object-contain" />
        </div>
      </div>

      {/* Main Content */}
      <div className="flex flex-1 w-full max-w-7xl mx-auto px-8 py-12 gap-8">
        {/* Left Column - Form */}
        <div className="w-1/2">
          <h1 className="text-3xl font-bold text-gray-900 mb-6">
            What income did your company have?
          </h1>

          <p className="text-lg text-gray-700 mb-8">
            Please input any{' '}
                <span className="text-green-500 font-bold">
                  income related to sales, 
                </span>{' '}
                <span className="text-red-500 font-bold">
                  no contributions from partners:
                </span>{' '}
          </p>

          <div className="space-y-6">
            {/* Services Input */}
            <div className="space-y-2">
              <div className="flex items-center gap-2">
                <label className="block text-sm font-medium text-gray-700">
                  Services (intangible)
                </label>
                <div className="relative group">
                  <HelpCircle size={16} className="text-red-400 cursor-help" />
                  <div className="absolute left-1/2 -translate-x-1/2 bottom-full mb-2 w-64 p-2 bg-gray-800 text-white text-sm rounded-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-opacity">
                    Income from services provided to customers, such as consulting, maintenance, repairs, or any non-physical product delivery.
                    <div className="absolute left-1/2 -bottom-1 -translate-x-1/2 w-2 h-2 bg-gray-800 rotate-45"></div>
                  </div>
                </div>
              </div>
              <div className="relative">
                <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
                <input
                  type="text"
                  value={formData.services}
                  onChange={(e) => handleInputChange('services', e.target.value)}
                  onBlur={() => handleInputBlur('services')}
                  className="w-full pl-8 pr-4 py-2 border rounded-lg focus:ring-2 focus:ring-teal-500 focus:border-teal-500"
                />
              </div>
            </div>

            {/* Products Input */}
            <div className="space-y-2">
              <div className="flex items-center gap-2">
                <label className="block text-sm font-medium text-gray-700">
                  Products (Tangibles)
                </label>
                <div className="relative group">
                  <HelpCircle size={16} className="text-red-400 cursor-help" />
                  <div className="absolute left-1/2 -translate-x-1/2 bottom-full mb-2 w-64 p-2 bg-gray-800 text-white text-sm rounded-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-opacity">
                    Income from selling physical goods or products to customers, including manufactured items, resale goods, or any tangible merchandise.
                    <div className="absolute left-1/2 -bottom-1 -translate-x-1/2 w-2 h-2 bg-gray-800 rotate-45"></div>
                  </div>
                </div>
              </div>
              <div className="relative">
                <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
                <input
                  type="text"
                  value={formData.products}
                  onChange={(e) => handleInputChange('products', e.target.value)}
                  onBlur={() => handleInputBlur('products')}
                  className="w-full pl-8 pr-4 py-2 border rounded-lg focus:ring-2 focus:ring-teal-500 focus:border-teal-500"
                />
              </div>
            </div>

            {/* Interest Revenue Input */}
            <div className="space-y-2">
              <div className="flex items-center gap-2">
                <label className="block text-sm font-medium text-gray-700">
                  Interest revenue
                </label>
                <div className="relative group">
                  <HelpCircle size={16} className="text-red-400 cursor-help" />
                  <div className="absolute left-1/2 -translate-x-1/2 bottom-full mb-2 w-64 p-2 bg-gray-800 text-white text-sm rounded-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-opacity">
                    Income earned from interest-bearing accounts, investments, loans provided, or other financial instruments generating interest.
                    <div className="absolute left-1/2 -bottom-1 -translate-x-1/2 w-2 h-2 bg-gray-800 rotate-45"></div>
                  </div>
                </div>
              </div>
              <div className="relative">
                <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
                <input
                  type="text"
                  value={formData.interest}
                  onChange={(e) => handleInputChange('interest', e.target.value)}
                  onBlur={() => handleInputBlur('interest')}
                  className="w-full pl-8 pr-4 py-2 border rounded-lg focus:ring-2 focus:ring-teal-500 focus:border-teal-500"
                />
              </div>
            </div>

            {/* Other Income Section */}
            <div className="space-y-4">
              <div className="flex items-center gap-2">
                <h3 className="text-lg font-medium text-gray-900">Other Income</h3>
                <div className="relative group">
                  <HelpCircle size={16} className="text-red-400 cursor-help" />
                  <div className="absolute left-1/2 -translate-x-1/2 bottom-full mb-2 w-64 p-2 bg-gray-800 text-white text-sm rounded-lg opacity-0 invisible group-hover:opacity-100 group-hover:visible transition-opacity">
                    Any additional income that doesn't fit into the above categories, such as rental income, royalties, commissions, or other miscellaneous revenue streams.
                    <div className="absolute left-1/2 -bottom-1 -translate-x-1/2 w-2 h-2 bg-gray-800 rotate-45"></div>
                  </div>
                </div>
              </div>

              {/* Add New Item Button */}
              <button
                onClick={() => setIsModalOpen(true)}
                className="flex items-center gap-2 text-teal-500 hover:text-teal-600"
              >
                <Plus size={20} />
                Add a new item
              </button>

              {/* Other Incomes List */}
              <div className="space-y-4">
                {otherIncomes.map((income) => (
                  <div 
                    key={income.id}
                    className="flex items-center gap-4"
                  >
                    <div className="flex-1">
                      <label className="block text-sm font-medium text-gray-700 mb-1">
                        {income.description}
                      </label>
                      <div className="relative">
                        <span className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500">$</span>
                        <input
                          type="text"
                          value={income.amount}
                          onChange={(e) => handleOtherAmountChange(income.id, e.target.value)}
                          onBlur={() => handleOtherAmountBlur(income.id)}
                          className="w-full pl-8 pr-4 py-2 border rounded-lg focus:ring-2 focus:ring-teal-500"
                        />
                      </div>
                    </div>
                    <button
                      onClick={() => handleRemoveIncome(income.id)}
                      className="text-gray-400 hover:text-red-500 transition-colors mt-6"
                    >
                      <Trash2 size={20} />
                    </button>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Progress and Navigation */}
          <div className="mt-12">
            <div className="flex items-center mb-4">
              <div className="text-sm text-gray-500">Step 2 of 9</div>
              <div className="ml-4 flex-1 h-2 bg-gray-200 rounded-full">
                <div
                  className="h-full bg-teal-500 rounded-full transition-all duration-300 ease-in-out"
                  style={{ width: "22.22%" }}
                ></div>
              </div>
            </div>
            <div className="flex gap-4">
              <button 
                onClick={handlePrevious}
                className="px-6 py-2 rounded-lg bg-purple-600 text-white flex items-center gap-2 hover:bg-purple-700 transition-colors"
              >
                <ArrowLeft size={20} />
                Previous
              </button>
              <button 
                onClick={handleNext}
                className="px-6 py-2 rounded-lg bg-teal-500 text-white flex items-center gap-2 hover:bg-teal-600 transition-colors"
              >
                Next
                <ArrowRight size={20} />
              </button>
            </div>
          </div>
        </div>

        {/* Right Column - Summary */}
        <div className="w-1/2 p-8 rounded-lg" style={{ backgroundColor: '#F7F7F7' }}>
          <h2 className="text-2xl font-bold text-gray-900 mb-8 text-center">
          2023 SUMMARY NET INCOME
          </h2>

          <div className="space-y-4">
            <div className="flex justify-between p-4 rounded-lg" style={{ backgroundColor: 'rgba(65, 191, 120, 0.19)' }}>
              <span className="font-bold">TOTAL REVENUE</span>
              <span className="font-bold">${summary.totalRevenue}</span>
            </div>

            <div className="flex justify-between p-4 bg-white rounded-lg">
              <span className="font-medium">TOTAL COSTS</span>
              <span className="font-medium">${summary.totalCosts}</span>
            </div>

            <div className="flex justify-between p-4 bg-white rounded-lg">
              <span className="font-medium">TOTAL EXPENSES</span>
              <span className="font-medium">${summary.totalExpenses}</span>
            </div>

            <div className="flex justify-between p-4 rounded-lg" style={{ backgroundColor: 'rgba(65, 191, 120, 0.19)' }}>
              <span className="font-bold">NET INCOME</span>
              <span className="font-bold">${summary.netIncome}</span>
            </div>
          </div>
        </div>
      </div>

      {/* Modal */}
      <AnimatePresence>
        {isModalOpen && (
          <AddIncomeModal
            isOpen={isModalOpen}
            onClose={() => setIsModalOpen(false)}
            onAdd={handleAddIncome}
          />
        )}
      </AnimatePresence>
    </motion.div>
  );
};

export default IncomeForm;